.product-wrap {
   display: flex;
   justify-content: space-between;
   max-width: 620px;
   margin-bottom: 35px;

   &__title {
      font-weight: 600;
      font-size: 22px;
      line-height: 130%;
      max-width: 365px;
      width: 100%;
      min-height: 56px;
   }
   &__price {
      font-weight: 700;
      font-size: 36px;
      line-height: 36px;
   }
}

.multy-quantity input {
   background: var(--color-bg);
   text-align: center;
   border: none;
   width: 71px;
   height: 52px;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
}

.multy-right {
   .card__raiting {
      margin-bottom: 37px;
   }
   .card-price {
      align-items: baseline;
      max-width: 340px;
      margin-bottom: 76px;
      &__title {
         margin-bottom: 0;
         font-weight: 700;
         font-size: 42px;
         line-height: 130%;
      }
      &__from {
         font-weight: 700;
         font-size: 28px;
         line-height: 130%;
      }
   }
   .description-right__buttons {
      margin-top: 60px;
   }
   .buy {
      margin-right: 50px;
   }
   .favorite {
      margin-right: 50px;
   }
}
