html {
   box-sizing: border-box;
   scroll-behavior: smooth;
}

*,
*::before,
*::after {
   box-sizing: inherit;
}

body {
   font-family: "Akrobat", sans-serif;
   font-weight: 600;
   background: var(--color-bg);
   color: var(--color-dark);

   &.lock {
      overflow: hidden;
   }
}

img {
   max-width: 100%;
}

a {
   text-decoration: none;
   color: inherit;
   outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
   margin: 0;
   padding: 0;
}

ul,
ol {
   list-style: none;
   margin: 0;
   padding: 0;
}

button {
   border: none;
   background: transparent;
   cursor: pointer;
   padding: 0;
   outline: none;
}

input,
textarea {
   outline: none;

   &:focus::-webkit-input-placeholder {
      opacity: 0;
      transition: opacity 0.3s;
   }

   &:focus::-moz-placeholder {
      opacity: 0;
      transition: opacity 0.3s;
   }

   &:focus:-moz-placeholder {
      opacity: 0;
      transition: opacity 0.3s;
   }

   &:focus:-ms-input-placeholder {
      opacity: 0;
      transition: opacity 0.3s;
   }

   &[type="password"] {
      background: url("../img/svg/password.svg") no-repeat right center;
   }
}

.is-outer {
   position: relative !important; // stylelint-disable-line declaration-no-important
   left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
   overflow: hidden;
}

.is-hidden {
   display: none !important; // stylelint-disable-line declaration-no-important
}

.error {
   border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
   position: absolute;
   clip: rect(0 0 0 0);
   width: 1px;
   height: 1px;
   margin: -1px;
}

.sitemap {
   padding-top: 40px;
   padding-bottom: 40px;

   .main-menu {
      display: block;

      &__item {
         margin-bottom: 30px;
      }
   }

   .main-menu:before {
      display: none;
   }

   .main-menu li:not(:last-child) {
      margin-right: 0;
   }
}

.container {
   max-width: 1750px;
   padding-left: 25px;
   padding-right: 25px;
   width: 100%;
   margin: 0 auto;
   position: relative;
}

.page-container {
   max-width: 1700px;
   width: 100%;
   margin: 0 auto;
}

.slick-arrow {
   position: absolute;
   font-size: 0;
   background: none;
   border: none;
   cursor: pointer;

   &:after,
   &:before {
      display: none;
   }
}

.slick-initialized .slick-slide {
   min-height: 0;
   outline: none;
}

.slick-slider {
   margin-bottom: 0;
}

.scroll {
   display: block;
   position: absolute;
   transform-origin: 100% 100%;
   transform: rotate(-90deg);
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-align: right;
   right: 26px;

   &:before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
   }
}

.btn {
   display: flex;
   align-items: center;
   justify-content: center;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   color: var(--color-red);
   background: transparent;
   padding: 15px 20px;
   border: 1px solid var(--color-red);
   position: relative;
   max-width: 217px;
   width: 100%;
   transition: color 0.3s, background-color 0.3s;
   cursor: pointer;

   &:hover {
      color: #fff;
      background-color: var(--color-red);
      transition: color 0.3s, background-color 0.3s;
   }
}

.link {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   padding: 13px 41px 13px 48px;
   transition: color 0.3s;

   span {
      display: block;
      position: relative;
      padding-left: 25px;

      &::before {
         content: "";
         position: absolute;
         left: 0;
         top: 0;
         width: 2px;
         height: 18px;
         background-color: currentColor;
         transition: background-color 0.3s;
         transform: rotate(0);
         transition: transform 0.3s, background-color 0.3s;
      }

      &::after {
         content: "";
         position: absolute;
         left: -8px;
         top: 8px;
         width: 18px;
         height: 2px;
         background-color: currentColor;
         transform: rotate(0);
         transition: transform 0.3s, background-color 0.3s;
      }
   }

   &:hover {
      transition: color 0.3s;

      span {
         &::after {
            transform: rotate(180deg);
            transition: transform 0.3s;
         }

         &::before {
            transform: rotate(180deg);
            transition: transform 0.3s;
         }
      }
   }
}

.title {
   font-weight: 600;
   font-size: 42px;
   line-height: 130%;
   max-width: max-content;
   padding-bottom: 17px;
   text-transform: uppercase;
   color: var(--color-dark);
   position: relative;

   &:after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 2px;
      background: var(--color-dark);
   }

   span {
      color: var(--color-gray);
   }
}

.section-title {
   font-family: "Akrobat", sans-serif;
   font-weight: 700;
   font-size: 32px;
   line-height: 32px;
   color: #434343;
   padding-left: 48px;
   margin-left: 47px;
   margin-bottom: 30px;
   text-align: left;
   position: relative;

   &:before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
   }
}

.page-title {
   font-weight: 700;
   font-size: 42px;
   line-height: 130%;
   text-transform: uppercase;
   margin-bottom: 33px;
}

.counter {
   font-weight: 600;
   font-size: 22px;
   line-height: 20px;
   position: absolute;
   transform-origin: 0 0;
   text-transform: uppercase;

   &__right {
      font-size: 16px;
      color: var(--color-gray);
   }
}

.favorite {
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   color: var(--color-dark);
   padding: 13px 10px;
   display: flex;
   align-items: center;
   transition: color 0.3s;

   &:hover {
      color: var(--color-red);
      transition: color 0.3s;
   }

   span {
      position: relative;
      padding-left: 39px;
   }

   &:before {
      content: "";
      display: block;
      position: absolute;
      width: 28px;
      height: 26px;
   }
}

.buy {
   display: flex;
   align-items: center;
   justify-content: center;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   color: #e44747;
   padding: 15px 20px;
   border: 1px solid#e44747;
   position: relative;
   max-width: 217px;
   width: 100%;
   transition: color 0.3s, background-color 0.3s;

   span {
      position: relative;
      padding-left: 28px;

      &:before {
         content: "";
         display: block;
         position: absolute;
         left: 0;
         bottom: 0;
         background: url("../img/svg/cart-red.svg") no-repeat center center/ contain;
         width: 19px;
         height: 18px;
         transition: background-image 0.3s;
      }
   }

   &:hover {
      color: #fff;
      background-color: var(--color-red);

      span::before {
         background: url("../img/svg/cart-white.svg") no-repeat center center/ contain;
      }
   }
}

.go-up {
   text-align: right;
   margin-bottom: 30px;

   &__link {
      display: inline-block;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-dark);
      border: 2px solid var(--color-dark);
      padding: 14px 17px 14px 30px;

      svg {
         transform: translateY(0);
         transition: transform 0.3s;
         margin-right: 10px;

         path {
            transition: fill 0.3s;
         }
      }

      &:hover {
         color: var(--color-red);
         border: 2px solid currentColor;
         transition: color 0.3s, border 0.3s;

         svg {
            transform: translateY(-2px);
            transition: transform 0.3s;

            path {
               fill: var(--color-red);
               transition: fill 0.3s;
            }
         }
      }
   }
}

.radio {
   &__text {
      position: relative;
      user-select: none;
      cursor: pointer;

      &:before {
         content: "";
         display: block;
         position: absolute;
         bottom: 2px;
         width: 16px;
         height: 16px;
         left: 0;
         border: 1px solid #8f8f8f;
         border-radius: 100px;
      }
   }

   &__input {
      &:checked + label {
         &:before {
            border: 5px solid var(--color-red);
         }
      }
   }
}

.flex {
   display: flex;
}

.justify {
   justify-content: space-between;
}

.center {
   align-items: center;
}

.ref {
   font-weight: 500;
   font-size: 22px;
   line-height: 12px;
   letter-spacing: 0.06em;
   color: #8f8f8f;
   border-bottom: 1px solid #8f8f8f;
   transition: color 0.3s, border-color 0.3s;

   &:hover {
      color: var(--color-dark);
      border-bottom: 1px solid var(--color-dark);
      transition: color 0.3s, border-color 0.3s;
   }
}

.form {
   padding-top: 30px;
   &__title {
      font-weight: 700;
      font-size: 32px;
      line-height: 130%;
      text-align: center;
      text-transform: uppercase;
   }
   &-group {
      display: flex;
      justify-content: space-between;
      margin-bottom: 40px;
   }
   &__input {
      width: 45%;
      font-weight: 600;
      line-height: 175%;
      padding-left: 17px;
      border: 0;
      border-bottom: 2px solid #434343;
      font-size: 18px;
      padding-bottom: 8px;
      color: var(--color-dark);
      &::placeholder {
         color: #606060;
      }
   }
   &__textarea {
      font-weight: 600;
      font-size: 18px;
      line-height: 175%;
      padding-left: 17px;
      border: 2px solid #434343;
      width: 100%;
      min-height: 170px;
      margin-bottom: 3px;
      color: var(--color-dark);
   }
}

.close {
   position: relative;
   width: 30px;
   height: 30px;
   color: #fff;
   &:before,
   &:after {
      content: "";
      position: absolute;
      display: block;
      background: currentColor;
      width: 100%;
      height: 2px;
      left: 0;
      transition: transform 0.2s linear;
   }
   &:before {
      top: 14px;
      transform: rotate(45deg);
   }

   &:after {
      bottom: 14px;
      transform: rotate(-45deg);
   }
   &:hover {
      &:before,
      &:after {
         background-color: var(--color-red);
      }
   }
}
