.how {
   padding-top: 70px;
   margin-bottom: 50px;
}

.how-list {
   display: flex;
   flex-wrap: wrap;
   list-style-type: none;
   padding-left: 0;
   counter-reset: my-counter;
   margin-right: -5%;

   &__item {
      width: 20%;
      text-align: center;
      position: relative;
      margin-right: 5%;
      padding-right: 45px;
      &:last-child::after {
         display: none;
      }

      &:before {
         content: "0" counter(my-counter);
         counter-increment: my-counter;
         display: block;
         position: absolute;
         top: 0;
         left: 0;
         font-weight: 600;
         font-size: 14px;
         line-height: 20px;
         letter-spacing: 0.06em;
         color: #949494;
      }
      &:after {
         content: "";
         display: block;
         position: absolute;
         right: -10px;
         top: 30px;
         width: 15px;
         height: 30px;
         background: url("../img/svg/arrow-right.svg") no-repeat center center / contain;
      }
   }
   &__img {
      padding: 10px 0 15px;
      text-align: center;
   }
   &__text {
      font-weight: 700;
      font-size: 16px;
      line-height: 170%;
   }
}
