	.modal-left {
		padding: 30px 15px 20px;
	}
	.modal-form {
		.form__input {
			margin-bottom: 10px;
			font-size: 16px;
		}
		.form__textarea {
			font-size: 16px;
			margin-bottom: 10px;
		}
		.form-group__upload {
			text-align: left;
		}
	}
	.modal {
		&__title {
			font-size: 20px;
		}
		&__text {
			font-size: 14px;
		}
	}
	.multy-right .card-price {
		&__title {
			font-size: 30px;
		}
		margin-bottom: 30px;
	}
	.product-wrap {
		flex-wrap: wrap;
		&__title {
			max-width: 210px;
		}
		.quantity {
			margin-bottom: 20px;
		}
	}
	.products {
		&.columns-1 {
			.card {
				flex-direction: column;
			}
			.woocommerce-loop-product__link {
				padding-right: 0;
			}

			.card-wrap {
				width: 100%;
			}
			.card__bottom {
				padding: 0;
			}
		}
	}
	.article {
		&__container {
			padding-left: 15px;
			padding-right: 15px;
		}
		h1 {
			font-size: 30px;
		}
		p {
			font-size: 14px;
		}
	}
	.wp-block-button__link {
		font-size: 16px;
	}
	.article-top__img--title {
		font-size: 26px;
		left: auto;
		right: 10px;
	}
	.article-right {
		padding-left: 0;
		ul li {
			font-size: 16px;
		}
		h3 {
			font-size: 26px;
		}
	}
	.article-left h2 {
		font-size: 25px;
	}
	figure.columns-3 ul li {
		width: 100%;
	}
	figure.columns-2 ul li {
		width: 100%;
	}
	.wp-block-table .table {
		thead tr {
			th {
				padding: 10px;
				font-size: 14px;
			}
		}
		tbody tr {
			td {
				font-size: 14px;
				padding: 10px;
				&:first-child {
					font-size: 14px;
					padding: 10px;
				}
			}
		}
	}
	.news__container {
		padding-left: 0;
		padding-right: 0;
	}
	.page-projects {
		padding-top: 30px;
		padding-bottom: 0;
	}
}
