.breadcrumbs {
   display: flex;
   align-items: center;
   padding: 25px 0;
   &__item {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: #434343;
      display: flex;
      align-items: center;
      margin-right: 18px;
      transition: color 0.3s;
      &:last-child {
         color: #949494;
         pointer-events: none;
      }
      &:hover {
         color: var(--color-red);
         svg path {
            fill: var(--color-red);
            stroke: var(--color-red);
         }
      }
   }
   &-separator {
      margin-right: 16px;
      display: flex;
      align-items: center;
      svg {
         width: 8px;
         height: 16px;
      }
   }
}
