.selected {
   padding-top: 24px;
   margin-bottom: 80px;
   &__wrap {
      display: flex;
      justify-content: space-between;
   }
   &-left {
      width: 66%;
      background: #fff;
      padding: 0 70px;
   }
   &-right {
      width: 32.5%;
   }
}

.selected-table {
   tbody {
      .product-name {
         max-width: 200px;
      }
   }
}
