.technical {
   padding-top: 28px;

   &__title {
      &:before {
         background: url("../img/svg/technical.svg") no-repeat center center;
      }
   }

   &__table {
      padding-bottom: 20px;
      overflow-x: auto;
      background-color: #fff;
   }
   &__link {
      color: var(--color-red);
      transition: color 0.3s;
      padding-left: 55px;
      &:hover {
         color: var(--color-dark);
         transition: color 0.3s;
      }
   }
}

.table {
   background: #fff;
   width: 100%;
   border-collapse: collapse;
   cursor: pointer;
   margin-bottom: 18px;

   th {
      font-weight: 800;
      font-size: 20px;
      line-height: 150%;
      color: #4c4c4c;
      padding-left: 40px;
      padding-right: 40px;
      text-align: left;
      border: 1px solid #e5e5e5;
   }

   thead tr th {
      max-width: 131px;
      text-align: center;
      padding-top: 23px;
      padding-bottom: 23px;

      &:first-child {
         text-align: left;
         max-width: 150px;
      }
   }

   tbody tr th {
      color: #747474;
      padding-left: 40px;
      padding-right: 40px;

      max-width: 200px;
   }

   td {
      font-weight: bold;
      font-size: 20px;
      line-height: 150%;
      text-align: center;
      color: #747474;
      text-align: center;
      padding: 46px 10px;
      border: 1px solid #e5e5e5;
   }

   tr {
      border: 1px solid #e5e5e5;
      transition: background-color 0.3s;

      &:hover {
         background-color: rgba(248, 128, 128, 0.2);
         transition: background-color 0.3s;
      }
   }
}
