.vacancies {
   padding-top: 50px;
   padding-bottom: 22px;
   &__title {
      margin-bottom: 97px;
   }
   &__subtitle {
      font-weight: 700;
      font-size: 32px;
      line-height: 130%;
      text-transform: uppercase;
      margin-bottom: 36px;
   }
   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
      margin-bottom: 27px;
   }
   &-mail {
      margin-bottom: 63px;
   }
}

.vacancies-mail {
   font-weight: 700;
   font-size: 16px;
   line-height: 170%;
   &__link {
      font-weight: 500;
   }
}

.vacancies-list {
   list-style: decimal-leading-zero;
   padding-top: 5px;
   padding-left: 20px;
   margin-bottom: -10px;
   &__item {
      margin-bottom: 30px;
   }
}
