.header-top {
   position: relative;
   z-index: 100;
   padding: 7px 0 11px;
   background-color: var(--color-bg);

   &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   &__viber {
      margin-left: auto;
      margin-right: 43px;
      color: var(--color-gray);
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      letter-spacing: 0.06em;
      transition: color 0.3s;

      &:hover {
         color: var(--color-red);
         transition: color 0.3s;

         .header-top__viber--phone {
            color: var(--color-red);
            transition: color 0.3s;
         }
      }

      &--phone {
         color: var(--color-dark);
         margin-left: 5px;
      }
   }

   &__phone {
      display: block;
      border: 2px solid var(--color-dark);
      padding: 8px 12px;
      padding-left: 13px;
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      letter-spacing: 0.06em;
      transition: color 0.3s, border 0.3s;

      svg {
         width: 14px;
         height: 15px;
         margin-right: 3px;
         transition: fill 0.3s;
      }

      &:hover {
         color: var(--color-red);
         border: 2px solid currentColor;
         transition: color 0.3s, border 0.3s;

         svg path {
            fill: var(--color-red);
            transition: fill 0.3s;
         }
      }
   }
}

.lang {
   display: flex;

   &__item {
      margin-right: 10px;
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-gray);
      transition: color 0.3s;

      &.current-lang {
         color: var(--color-dark);
         font-weight: 600;
         border-bottom: 2px solid var(--color-dark);
      }

      &:hover {
         color: var(--color-dark);
         font-weight: 600;
         border-bottom: 2px solid var(--color-dark);
         transition: color 0.3s;
      }
   }
}

.header-bottom {
   position: relative;
   z-index: 100;
   background: #ffffff;

   &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 13px 25px;
   }

   &__left {
      display: flex;
      align-items: center;
      max-width: 454px;
      width: 100%;
      padding-bottom: 5px;
   }

   &__title {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 22px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-dark);
      margin-right: 63px;
      transition: color 0.3s;

      &.hidden {
         display: none;
      }
   }

   &__logo {
      margin-right: 140px;
   }
}

.cabinet {
   display: flex;
   align-items: center;

   &__link {
      display: inline-block;

      svg path {
         transition: fill 0.3s, stroke 0.3s;
      }
   }

   &__item {
      position: relative;
      padding: 20px;
      margin-right: 20px;
      cursor: pointer;

      &:hover {
         svg path {
            fill: var(--color-red);
            stroke: var(--color-red);
            transition: fill 0.3s, stroke 0.3s;
         }

         .cart__quantity {
            background-color: var(--color-red);
            transition: background-color 0.3s;
         }
      }

      &:last-child {
         margin-right: 5px;
      }
   }
}

.cart {
   position: relative;

   &__icon {
      display: inline-block;
   }

   &__quantity {
      position: absolute;
      right: -7px;
      top: -7px;
      display: inline-flex;
      align-items: center;
      color: #fff;
      border-radius: 25px;
      height: 17px;
      padding: 0 5px;
      background-color: var(--color-dark);
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 12px;
      text-align: center;
      transition: background-color 0.3s;
   }
}

.searh-form {
   max-width: 268px;
   width: 100%;
   display: flex;
   align-items: center;

   svg {
      width: 24px;
      height: 24px;
   }

   &__input {
      width: 100%;
      border: none;
      height: 35px;
      border-bottom: 2px solid var(--color-dark);
      font-weight: 600;
      font-size: 22px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-dark);
      padding-left: 37px;
      background: url("../img/svg/search.svg") no-repeat left center;
      opacity: 1;
      transition: opacity 0.3s;

      &:focus {
         background-image: url("../img/svg/search-focus.svg");
         border-bottom: 2px solid var(--color-red);
      }
   }
}

.header-menu {
   position: absolute;
   left: 0;
   right: 0;
   background: #fff;
   box-shadow: 0px 10px 27px -13px rgba(0, 0, 0, 0.1) inset;
   padding-top: 152px;
   padding-bottom: 150px;
   transform: translateY(-130%);
   transition: transform 0.6s;
   z-index: 90;
}

.burger {
   width: 36px;
   height: 30px;
   margin-right: 16px;
   flex-shrink: 0;
   position: relative;

   &__wrap {
      display: flex;
      align-items: center;
      cursor: pointer;

      &:hover {
         .header-bottom__title {
            color: var(--color-red);
            transition: color 0.3s;
         }

         .burger {
            span,
            &:before,
            &:after {
               background-color: var(--color-red);
               transition: background-color 0.3s;
            }
         }
      }
   }

   span,
   &:before,
   &:after {
      position: absolute;
      display: block;
      content: "";
      background-color: var(--color-dark);
      width: 100%;
      height: 2px;
      left: 0;
      transition: transform 0.2s linear, background-color 0.3s;
   }

   span {
      top: 50%;
      transform: scale(1) translateY(-50%);
   }

   &:before {
      top: 0;
      transform: translateY(7px);
   }

   &:after {
      bottom: 0;
      width: 17px;
      transform: translateY(-7px);
   }
}

.header-nav {
   display: flex;
   justify-content: space-between;
   margin-bottom: 215px;
   padding-left: 50px;

   &__item {
      width: 33.3%;
      padding-left: 200px;

      ul {
         font-weight: 600;
         font-size: 22px;
         line-height: 20px;
         letter-spacing: 0.06em;
         text-transform: uppercase;

         li {
            position: relative;
            transition: color 0.3s;

            &:hover {
               color: var(--color-red);
               transition: color 0.3s;
            }

            &:nth-child(3n + 1) {
               &:before {
                  background: url("../img/svg/menu-1.1.svg") no-repeat center center;
               }
            }

            &:nth-child(3n + 2) {
               &:before {
                  background: url("../img/svg/menu-1.2.svg") no-repeat center center;
               }
            }

            &:nth-child(3n + 3) {
               &:before {
                  background: url("../img/svg/menu-1.3.svg") no-repeat center center;
               }
            }

            &:before {
               content: "";
               display: block;
               position: absolute;
               left: -50px;
               top: -8px;
               width: 32px;
               height: 32px;
            }

            &:not(:last-child) {
               margin-bottom: 26px;
            }
         }
      }
   }

   &__title {
      font-weight: 700;
      font-size: 30px;
      line-height: 32px;
      text-transform: uppercase;
      padding-bottom: 11px;
      margin-bottom: 45px;
      position: relative;

      &:before {
         content: "";
         display: block;
         position: absolute;
         background: url("../img/svg/menu-1.svg") no-repeat center center;
         left: -50px;
         top: 0;
         width: 32px;
         height: 32px;
      }

      &:after {
         width: 50%;
      }
   }
}

.header-nav__center {
   .header-nav__title {
      &:before {
         background: url("../img/svg/menu-2.svg") no-repeat center center;
      }
   }

   ul li {
      &:nth-child(3n + 1) {
         &:before {
            background: url("../img/svg/menu-2.1.svg") no-repeat center center;
         }
      }

      &:nth-child(3n + 2) {
         &:before {
            background: url("../img/svg/menu-2.2.svg") no-repeat center center;
         }
      }

      &:nth-child(3n + 3) {
         &:before {
            background: url("../img/svg/menu-2.3.svg") no-repeat center center;
         }
      }
   }
}

.header-nav__right {
   .header-nav__title {
      &:before {
         background: url("../img/svg/menu-3.svg") no-repeat center center;
      }
   }
}

.main-menu {
   display: flex;
   justify-content: center;
   position: relative;

   &:before {
      content: "";
      display: block;
      position: absolute;
      top: -55px;
      left: 50%;
      transform: translateX(-50%);
      max-width: 556px;
      width: 100%;
      background: var(--color-dark);
      height: 1px;
   }

   li {
      font-weight: 600;
      font-size: 22px;
      line-height: 20px;
      text-align: center;
      text-transform: uppercase;

      &:hover {
         color: var(--color-red);
         transition: color 0.3s;
      }

      &:not(:last-child) {
         margin-right: 67px;
      }
   }
}

/*  Open menu  */

.header-menu {
   &.active {
      display: block;
      transform: translateY(0);
      transition: transform 0.6s;
      height: 100%;
      overflow-y: auto;

      .header {
         height: auto;
      }
   }
}

.burger.active {
   span {
      transform: scale(0);
   }

   &:before {
      transform: rotate(45deg) translateY(10px) translateX(10px);
   }

   &:after {
      width: 100%;
      transform: rotate(-45deg) translateY(-10px) translateX(10px);
   }

   span,
   &:before,
   &:after {
      transition: transform 0.2s linear;
   }
}

.cart-hidden {
   transform: scaleY(0);
   transform-origin: 50% 0%;
   position: absolute;
   right: -4px;
   top: 62px;
   width: 403px;
   box-shadow: 0px -22px 80px rgba(0, 0, 0, 0.03), 0px -9.19107px 33.4221px rgba(0, 0, 0, 0.0215656),
      0px -4.91399px 17.869px rgba(0, 0, 0, 0.0178832), 0px -2.75474px 10.0172px rgba(0, 0, 0, 0.015),
      0px -1.46302px 5.32008px rgba(0, 0, 0, 0.0121168), 0px -0.608796px 2.21381px rgba(0, 0, 0, 0.00843437);
   transition: transform 0.3s;
   .ordering-right {
      &__wrap {
         padding: 18px 20px;
         position: relative;
      }
      &__title {
         margin-bottom: 10px;
      }
      &__list {
         height: 436px;
      }
      &__bottom-wrap {
         padding-top: 24px;
      }
      &__close {
         position: relative;
         width: 30px;
         height: 30px;
         &:before,
         &:after {
            content: "";
            position: absolute;
            display: block;
            background: var(--color-dark);
            width: 100%;
            height: 2px;
            left: 0;
            transition: transform 0.2s linear;
         }
         &:before {
            top: 14px;
            transform: rotate(45deg);
         }

         &:after {
            bottom: 14px;
            transform: rotate(-45deg);
         }
         &:hover {
            &:before,
            &:after {
               background-color: var(--color-red);
            }
         }
      }
      &__link {
         margin-top: 15px;
      }
   }
   .goods-img {
      width: 45%;
   }
   .goods-title {
      max-width: 170px;
   }
   .goods-price {
      line-height: 13px;
      margin: 0;
   }
}
.cart-hover:hover {
   .cart-hidden {
      display: block;
      transform: scaleY(1);
   }
}
