.login {
   padding-top: 24px;
   margin-bottom: 50px;
   &__wrap {
      display: flex;
      align-items: flex-start;
   }
}

.login-left {
   width: 42.4%;
   background-color: #fff;
   &__form {
      max-width: 354px;
      margin: 0 auto;
      margin-bottom: 33px;
   }
   &__input {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
      text-transform: lowercase;
      width: 100%;
      height: 50px;
      opacity: 1;
      margin-bottom: 22px;
   }
   &__btn {
      color: var(--color-red);
      max-width: 100%;
   }
   &__remember {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      text-align: right;
      color: var(--color-dark);
      border-bottom: 1px solid var(--color-dark);
      &:hover {
         color: #8f8f8f;
         border-bottom: 1px solid #8f8f8f;
         transition: color 0.3s, border-color 0.3s;
      }
   }
   &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: end;
      margin-top: -4px;
      margin-bottom: 47px;
   }
}

.register {
   width: 721px;
   background-color: #fff;
   &__form {
      max-width: 354px;
      margin: 0 auto;
      margin-bottom: 33px;
   }
   &__input {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
      text-transform: lowercase;
      width: 100%;
      height: 50px;
      opacity: 1;
      margin-bottom: 22px;
   }
   &__btn {
      color: var(--color-red);
      max-width: 100%;
   }
   &__remember {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      text-align: right;
      color: var(--color-dark);
      border-bottom: 1px solid var(--color-dark);
   }
   &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: end;
      margin-top: -4px;
      margin-bottom: 47px;
   }
   &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 140%;
      color: #8f8f8f;
      margin-top: -5px;
      margin-bottom: 20px;
   }
}

.login-checkbox {
   &__text {
      font-family: "Akrobat", sans-serif;
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      color: var(--color-dark);
      transition: color 0.3s;
      &:hover {
         color: var(--color-red);
         transition: color 0.3s;
      }

      &:before {
         width: 20px;
         height: 20px;
         top: 2px;
         opacity: 1;
      }
   }
}

.tabs {
   padding: 5px 80px 38px;
   &__items {
      display: flex;
      margin-bottom: 38px;
   }
   &__item {
      font-weight: 500;
      font-size: 22px;
      line-height: 20px;
      text-align: center;
      letter-spacing: 0.06em;
      flex: 0 1 50%;
      padding: 30px 20px;
      border-bottom: 1px solid #edf1f4;

      &.active {
         border-bottom: 2px solid var(--color-red);
      }
   }
}

.tab-pane {
   display: none;
   &.active {
      display: block;
   }
}

.faded {
   opacity: 0;
   transition: all 0.5s;
}

.faded.in {
   opacity: 1;
   transition: all 0.5s;
}

.social-block {
   &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
      text-align: center;
      margin-bottom: 10px;
   }
}

.social {
   display: block;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   text-align: center;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   color: var(--color-dark);
   border: 2px solid var(--color-dark);
   max-width: 354px;
   margin: 0 auto;
   margin-bottom: 20px;
   padding: 14px 20px;
   transition: color 0.3s, border 0.3s;

   &:hover {
      border: 2px solid var(--color-red);
      color: var(--color-red);
      transition: color 0.3s, border 0.3s;
   }

   span {
      position: relative;
      padding-left: 40px;
      &:before {
         content: "";
         display: block;
         position: absolute;
         top: 0;
         left: 0;
         width: 24px;
         height: 24px;
      }
   }
   &--facebook span:before {
      background: url("../img/svg/facebook.svg") no-repeat center center;
   }
   &--google span:before {
      background: url("../img/svg/google.svg") no-repeat center center;
   }
}

.login-right {
   width: 57.6%;
   background: #f76f6f url("../img/svg/login-bg.svg") no-repeat center center;
   display: flex;
   padding: 110px 0 110px 160px;
   &__list {
      max-width: 500px;
   }
   &__item {
      margin-bottom: 60px;
      padding-left: 80px;
      position: relative;
      &::before {
         content: "";
         display: block;
         position: absolute;
         top: 0;
         left: 0;
         width: 59px;
         height: 59px;
      }
      &:nth-child(1)::before {
         background: url("../img/svg/login-right-1.svg") no-repeat center center;
      }
      &:nth-child(2)::before {
         background: url("../img/svg/login-right-2.svg") no-repeat center center;
      }
      &:nth-child(3)::before {
         background: url("../img/svg/login-right-3.svg") no-repeat center center;
      }
   }
   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 170%;
      color: #ffffff;
      margin-bottom: 7px;
   }
   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
      color: #ffffff;
   }
}
