.about-us {
   padding-top: 84px;
   &__top {
      display: flex;
      justify-content: space-between;
   }
   &__top-left {
      width: 51%;
   }
   &__top-right {
      position: relative;
      width: 49%;
   }
   &__main-title {
      line-height: 100%;
      margin-bottom: 23px;
   }
   &__title {
      font-weight: 600;
      font-size: 42px;
      line-height: 130%;
      text-transform: uppercase;
      position: relative;
      max-width: max-content;
      padding-bottom: 23px;
      margin-bottom: 38px;

      span {
         color: #949494;
      }
      &:after {
         content: "";
         display: block;
         position: absolute;
         bottom: 0;
         right: 0;
         width: 65px;
         height: 2px;
         background: var(--color-dark);
      }
   }
   &__text {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      text-transform: uppercase;
      margin-bottom: 62px;
   }

   &__wrap {
      display: flex;
      justify-content: space-between;
   }

   &__left {
      width: 66%;
      background: #fff;
      position: relative;
      &:before {
         content: "";
         display: block;
         background: #fff;
         position: absolute;
         top: 0;
         bottom: 0;
         left: -100%;
         width: 100%;
      }
   }

   &__right {
      width: 34%;
      position: relative;
   }
   &__descr {
      padding-top: 26px;
      background: #fff;
      position: relative;
      height: 100%;
      &:before {
         content: "";
         display: block;
         background: #fff;
         position: absolute;
         top: 0;
         bottom: 0;
         left: -100%;
         width: 100%;
      }
      p {
         max-width: 725px;
         font-weight: 500;
         font-size: 16px;
         line-height: 170%;
         margin-bottom: 30px;
         span {
            color: #949494;
         }
      }
   }
   &__link {
      margin-top: 17px;
      border: 2px solid var(--color-red);
      color: var(--color-red);
      transition: color 0.3s, border-color 0.3s;
      &:hover {
         border: 2px solid var(--color-dark);
         color: var(--color-dark);
      }
   }
   &__bottom {
      padding-top: 70px;
      padding-bottom: 86px;
   }
}

.about-us__slider {
   &-item {
      img {
         height: 568px;
         object-fit: cover;
      }
   }
   &-counter {
      z-index: 10;
      bottom: -24px;
      left: -56px;
      transform-origin: 0 0;
      transform: rotate(-90deg);
   }
   &-title {
      position: absolute;
      bottom: 35px;
      left: 22px;
      font-weight: 900;
      font-size: 42px;
      line-height: 130%;
      color: #fff;
   }
   .slick-arrow {
      left: -64px;
      width: 30px;
      height: 30px;
      z-index: 10;
      transform: translateX(0);
      transition: transform 0.3s;
   }

   .slick-prev {
      top: 60px;
      background: url("../img/svg/arrow-left.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-left-disabled.svg") no-repeat center top;
      }
   }

   .slick-prev:hover {
      transform: translateX(-5px);
      transition: transform 0.3s;
   }

   .slick-next {
      top: 0;
      background: url("../img/svg/arrow-right.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-right-disabled.svg") no-repeat center top;
      }
   }

   .slick-next:hover {
      transform: translateX(5px);
      transition: transform 0.3s;
   }
}
