.news {
   background: #fff;
   padding-bottom: 20px;
   &__title {
      margin-top: 35px;
      margin-bottom: 70px;
   }
   &__container {
      padding-left: 0;
      padding-right: 0;
   }
   &__paginations {
      background-color: var(--color-bg);
      justify-content: center;
      padding-bottom: 50px;
      .pagination-list {
         padding-left: 80px;
         padding-right: 60px;
      }
   }
}

.news-list {
   justify-content: start;
   margin-bottom: 30px;
   margin-left: 20px;
   &__item {
      margin-right: 41px;
      &:nth-child(1) {
         background-image: url("../img/svg/news-list-1.svg");
      }
      &:nth-child(2) {
         background-image: url("../img/svg/news-list-2.svg");
      }
      &:nth-child(3) {
         background-image: url("../img/svg/news-list-3.svg");
      }
      &:nth-child(4) {
         background-image: url("../img/svg/news-list-4.svg");
      }
   }
}

.news-card {
   &__list {
      display: flex;
      flex-wrap: wrap;
   }
   &__item {
      display: flex;
      flex-direction: column;
      width: 33.333%;
      cursor: pointer;
      padding: 10px 15px;
      margin-bottom: 45px;
      transition: box-shadow 0.3s;

      &:hover {
         box-shadow: 0px 22px 80px rgba(0, 0, 0, 0.08), 0px 10.4064px 10.0172px rgba(0, 0, 0, 0.04);
         img {
            filter: grayscale(100%);
         }
      }
   }
   &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: auto;
   }
   &__img {
      margin-bottom: 13px;
   }
   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      margin-bottom: 15px;
      letter-spacing: -0.006em;
   }

   &__text {
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 170%;
   }
   &__time {
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      color: #949494;
      border-bottom: 1px solid transparent;
      transition: border-color 0.3s;
      &:hover {
         border-bottom: 1px solid #949494;
         transition: border-color 0.3s;
      }
   }
   &__link {
      color: var(--color-red);
      padding-right: 0;
      transition: color 0.3s;
      &:hover {
         color: var(--color-dark);
         transition: color 0.3s;
      }
   }
   &__cat {
      font-weight: 500;
      font-size: 18px;
      line-height: 170%;
      letter-spacing: 0.09em;
      color: #727171;
      text-transform: uppercase;
      &:hover {
         color: var(--color-red);
      }
   }
}

.page-projects {
   padding-top: 71px;
   padding-bottom: 30px;
   box-shadow: 0px 10px 27px -13px rgba(0, 0, 0, 0.1) inset;
}

.projects {
   &__title {
      margin-bottom: 25px;
   }
   &__item {
      padding-top: 35px;
      padding-bottom: 30px;
      margin-bottom: 8px;
      &:hover {
         img {
            filter: none;
         }
      }
   }
   &-list {
      display: flex;
      margin-bottom: 10px;
   }
   &-card {
      padding-bottom: 20px;
   }
}
