.portfolio {
   padding-top: 50px;
   padding-right: 60px;
   margin-bottom: 50px;
   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
      margin-bottom: 75px;
   }
   &__bottom {
      position: relative;
   }
}

.portfolio-slider {
   padding-top: 43px;
   position: relative;

   &__img {
      margin-bottom: 12px;
      height: 260px;
      img {
         height: 100%;
      }
   }

   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
   }
   &__counter {
      top: -21px;
   }
   .slick-arrow {
      top: -23px;
      width: 30px;
      height: 30px;
      z-index: 10;
      transform: translateX(0);
      transition: transform 0.3s;
   }

   .slick-prev {
      right: 80px;
      background: url("../img/svg/arrow-left.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-left-disabled.svg") no-repeat center top;
      }
   }

   .slick-prev:hover {
      transform: translateX(-5px);
      transition: transform 0.3s;
   }

   .slick-next {
      right: 20px;
      background: url("../img/svg/arrow-right.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-right-disabled.svg") no-repeat center top;
      }
   }

   .slick-next:hover {
      transform: translateX(5px);
      transition: transform 0.3s;
   }
}
