.modal-overlay {
   position: fixed;
   left: 0;
   top: 0;
   right: 0;
   bottom: 0;
   background-color: rgba(0, 0, 0, 0.7);
   display: flex;
   align-items: center;
   justify-content: center;
   height: 100vh;
   width: 100%;
   opacity: 0;
   z-index: 110;
   visibility: hidden;
   transition: all 0.3s ease-in-out;
   &--visible {
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease-in-out;
   }
}

.modal {
   max-width: 1026px;
   width: 100%;
   justify-content: space-between;
   display: none;
   position: relative;
   &__title {
      font-weight: 600;
      font-size: 34px;
      line-height: 130%;
      padding-bottom: 21px;
      margin-bottom: 26px;
      &:after {
         width: 48%;
      }
      span {
         color: #949494;
      }
   }
   &__text {
      font-weight: 800;
      font-size: 18px;
      line-height: 150%;
      margin-bottom: 18px;
      span {
         font-weight: 500;
      }
   }
   &--visible {
      display: flex;
   }
   &__close {
      position: absolute;
      right: 12px;
      top: 13px;
      z-index: 10;
   }
}

.modal-left {
   background-color: #fff;
   max-width: 606px;
   padding: 46px 43px 20px;
}

.modal-right {
   background: var(--color-dark) url("../img/svg/modal-bg.svg") no-repeat center center / contain;
   position: relative;
   max-width: 419px;
   width: 100%;
   &__img {
      position: absolute;
      bottom: 0;
      left: -55px;
   }
}

.modal-form {
   .form-group {
      margin-bottom: 30px;
   }
   &__email {
      width: 100%;
      margin-bottom: 33px;
   }
   &__textarea {
      min-height: 136px;
      margin-bottom: 30px;
   }
   .form-group {
      align-items: center;
   }
   &__upload {
      cursor: pointer;
      padding-left: 47px;
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      position: relative;
      &::before {
         content: "";
         display: block;
         position: absolute;
         left: 0;
         top: 50%;
         transform: translateY(-50%);
         background: url("../img/svg/custom-file-input.svg") no-repeat left center / contain;
         width: 33px;
         height: 33px;
      }
   }
   &__btn {
      background: var(--color-red);
      max-width: 240px;
      color: #fff;
      &:hover {
         color: var(--color-red);
         background: #fff;
      }
   }
}

#upload-file {
   opacity: 0;
   position: absolute;
   z-index: -1;
}

.custom-file-input::-webkit-file-upload-button {
   visibility: hidden;
}
.custom-file-input::before {
   content: "";
   display: inline-block;
   background: url("../img/svg/custom-file-input.svg") no-repeat left center / contain;
   width: 33px;
   height: 33px;
   -webkit-user-select: none;
   cursor: pointer;
}
.custom-file-input:hover::before {
   border-color: black;
}
.custom-file-input:active::before {
   background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}
