.download {
   padding-top: 29px;
   margin-bottom: 30px;
   &__title {
      &:before {
         background: url("../img/svg/download.svg") no-repeat center center;
      }
   }
}

.download-list {
   margin-bottom: 28px;
   &__item {
      background: #fff;
      padding: 16px 100px 16px 46px;
      position: relative;
      &:not(:last-child) {
         border-bottom: 1px solid #edf1f4;
      }
   }
   &__title {
      font-weight: bold;
      font-size: 22px;
      line-height: 32px;
      letter-spacing: 0.09em;
   }
   &__text {
      font-weight: 500;
      font-size: 18px;
      line-height: 150%;
      color: #747474;
   }
   &__btn {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 53px;
      width: 30px;
      height: 30px;
   }
}

.download-question {
   background: #f76f6f url("../../img/svg/download-question-bg.svg") no-repeat center top / cover;
   text-align: center;
   margin-bottom: 30px;
   padding: 54px 0 57px;
   &__title {
      font-weight: bold;
      font-size: 32px;
      line-height: 170%;
      text-align: center;
      color: #ffffff;
      margin-bottom: 10px;
   }
   &__text {
      font-weight: 500;
      font-size: 18px;
      line-height: 170%;
      text-align: center;
      color: #ffffff;
      margin-bottom: 35px;
   }

   &__link {
      color: #fff;
      border: 2px solid #fff;
      padding: 13px 12px 13px 22px;
      &:hover {
         color: var(--color-dark);
         border: 2px solid currentColor;
         transition: color 0.3s, border 0.3s;
      }
   }
}
