.ordering {
   padding-top: 24px;

   &__form {
      margin-bottom: 50px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
   }
}

.ordering-left__personal,
.ordering-left__wrap,
.ordering-left__another {
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   max-width: 790px;
}

.ordering-left {
   width: 66%;
   padding: 34px 28px;
   background: #fff;
   &__input {
      font-family: "Akrobat", sans-serif;
      font-size: 18px;
      line-height: 230%;
      color: #8f8f8f;
      max-width: 354px;
      width: 100%;
      margin-bottom: 27px;
      background-color: transparent;
   }
   &__title {
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      margin-left: 18px;
      margin-bottom: 48px;
      padding-left: 34px;
      position: relative;
      &::before {
         content: "";
         display: block;
         position: absolute;
         top: 50%;
         transform: translateY(-50%);
         left: 0;
         width: 25px;
         height: 22px;
      }
      &--personal::before {
         background: url("../img/svg/ordering-1.svg") no-repeat left center;
      }
      &--delivery::before {
         background: url("../img/svg/ordering-2.svg") no-repeat left center;
      }
      &--recipient {
         margin-bottom: 0;
         &::before {
            background: url("../img/svg/ordering-3.svg") no-repeat left center;
         }
      }
   }

   &__personal {
      margin-bottom: 21px;
      margin-left: 50px;
   }
   &__delivery {
      background: var(--color-bg);
      padding-top: 40px;
      padding-bottom: 14px;
      margin-bottom: 46px;
   }
   &__another {
      display: none;
      margin-bottom: 8px;
      &.active {
         display: flex;
      }
   }
   &__link {
      font-weight: 600;
      font-size: 18px;
      line-height: 12px;
      letter-spacing: 0.06em;
      border-bottom: 1px solid var(--color-dark);
   }
   &__radio {
      display: flex;
      margin-bottom: 51px;
   }
   &__comment {
      padding-top: 40px;
      max-width: 522px;
      display: none;
   }
   &__bottom {
      padding-left: 50px;
   }
   &__wrap {
      margin-left: 50px;
   }
}

.recipient {
   display: flex;
   align-items: center;
   margin-bottom: 55px;
   &__info {
      position: relative;
      margin-left: 10px;
   }
   &__btn {
      position: relative;
      width: 21px;
      height: 21px;
      background: url("../img/svg/ordering-info.svg") no-repeat left center / contain;
      &::after {
         content: "";
         display: block;
         visibility: hidden;
         position: absolute;
         bottom: 30px;
         left: 50%;
         width: 16px;
         height: 19px;
         border-style: solid;
         border-width: 19px 16px 0 0;
         border-color: var(--color-red) transparent transparent transparent;
      }
      &:hover {
         .tooltip {
            visibility: visible;
         }
         &::after {
            visibility: visible;
         }
      }
   }
   .tooltip {
      width: 245px;
      background-color: var(--color-red);
      position: absolute;
      visibility: hidden;
      left: -103px;
      bottom: 48px;
      padding: 23px 5px 30px;
      text-align: center;
   }

   .tooltip .tooltiptext {
      font-weight: 600;
      font-size: 16px;
      line-height: 150%;
      color: #ffffff;
      z-index: 1;
   }
}

.delivery-group {
   max-width: 354px;
   width: 100%;
   position: relative;
   &__list {
      display: flex;
      justify-content: space-between;
      position: absolute;
      bottom: -3px;
      left: 21px;
      right: 16px;
   }
   &__home {
      display: flex;
      justify-content: space-between;
      max-width: 354px;
      width: 100%;
      margin-top: 37px;
      .form__input {
         max-width: 156px;
      }
   }
   &__town {
      font-weight: 600;
      font-size: 18px;
      line-height: 100%;
      border-bottom: 1px solid var(--color-dark);
      cursor: pointer;
      transition: border-color 0.3s, color 0.3s;
      &:hover {
         color: var(--color-red);
         border-bottom: 1px solid var(--color-red);
      }
   }
}

.ordering-radio {
   margin-right: 47px;
   &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 170%;
      padding-left: 25px;
   }
}

.ordering-right {
   width: 32.5%;
   &__wrap {
      padding: 34px 46px 1px 46px;

      background: #fff;
   }
   &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__title {
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
   }
   &__text {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
   }
   &__list {
      max-height: 448px;
      overflow-y: scroll;
   }
   &__bottom {
      display: none;
      width: 100%;
      background: #fff;

      box-shadow: 0px -22px 80px rgba(0, 0, 0, 0.03), 0px -9.19107px 33.4221px rgba(0, 0, 0, 0.0215656),
         0px -4.91399px 17.869px rgba(0, 0, 0, 0.0178832), 0px -2.75474px 10.0172px rgba(0, 0, 0, 0.015),
         0px -1.46302px 5.32008px rgba(0, 0, 0, 0.0121168), 0px -0.608796px 2.21381px rgba(0, 0, 0, 0.00843437);
      &-wrap {
         padding: 41px 0 44px;
         max-width: 300px;
         margin: 0 auto;
      }
      &.active {
         display: block;
      }
   }
   &__total {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 17px;
      &-text {
         font-family: "Open Sans", sans-serif;
         font-weight: 400;
         font-size: 16px;
         line-height: 170%;
      }
      &-price {
         font-weight: 900;
         font-size: 28px;
         line-height: 36px;
      }
   }
   &__btn {
      max-width: 100%;
   }
   &__link {
      margin-top: 30px;
      text-align: center;
   }
   &__empty {
      margin-top: 44px;
      margin-bottom: 60px;
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
   }
}

.goods {
   border-bottom: 1px solid #edf1f4;
   padding-bottom: 30px;
   padding-top: 30px;
   &-remove {
      display: block;
      background: url("../img/svg/trash.svg") no-repeat center center;
      width: 18px;
      height: 18px;
   }
   &-cod {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #606060;
      opacity: 0.7;
      margin-left: 13px;
   }
   &__top {
      margin-bottom: 10px;
   }
   &-title {
      max-width: 270px;
      font-weight: 600;
      font-size: 18px;
      line-height: 130%;
      margin-bottom: 12px;
   }
   &-img {
      padding-left: 32px;
      padding-right: 17px;
      width: 36%;
   }
   &-number {
      width: 51px;
      height: 38px;
      border: none;
      background: var(--color-bg);
      text-align: center;
   }
   &-price {
      font-weight: 700;
      font-size: 22px;
      line-height: 36px;
      margin-top: 10px;
      margin-right: 10px;
   }
}
