.contacts {
   background: #fff;
   box-shadow: 0px 10px 27px -13px rgba(0, 0, 0, 0.1) inset;
   padding-top: 70px;
   padding-bottom: 30px;
   &__top {
      display: flex;
      justify-content: space-between;
      padding-top: 57px;
      padding-bottom: 17px;
   }
   &__subtitle {
      font-weight: 700;
      font-size: 32px;
      line-height: 130%;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 33px;
   }
   &__text {
      display: inline-block;
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      text-align: center;
      letter-spacing: 0.06em;
   }
   &__link {
      &:hover {
         color: var(--color-red);
      }
   }
   &__tel {
      display: inline-block;
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      text-align: center;
      letter-spacing: 0.06em;
      padding-left: 32px;
      transition: color 0.3s;

      &--icon {
         position: relative;
         margin-right: 35px;

         &::before {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 14px;
            height: 15px;
            background: url("../img/svg/header-phone.svg") no-repeat left center / contain;
            transition: background-image 0.3s;
         }
         &:hover::before {
            background-image: url("../img/svg/header-phone-red.svg");
         }
      }
      &--viber {
         color: #959595;
         position: relative;
         padding-left: 50px;
         color: var(--color-dark);

         &::before {
            content: "viber";
            display: block;
            position: absolute;
            font-weight: 600;
            font-size: 18px;
            line-height: 18px;
            letter-spacing: 0.06em;
            color: #959595;
            left: 0;
         }
      }
      &:hover {
         color: var(--color-red);
         &::before {
            color: currentColor;
         }
      }
   }
   .form__btn {
      max-width: 100%;
   }
}

.contacts-left {
   text-align: center;
   width: 38%;
   &__block {
      margin-bottom: 59px;
   }
}

.contacts-right {
   width: 522px;
   margin-right: 140px;
}

.contacts-form {
   &__title {
      margin-bottom: 42px;
   }
   .form__input {
      width: 46%;
      padding-bottom: 8px;
   }
   .form-group {
      margin-bottom: 30px;
   }

   .form__textarea {
      margin-bottom: 27px;
   }
   &__btn {
      max-width: 100%;
   }
}

.contacts-map {
   text-align: center;
   padding-top: 133px;
   padding-bottom: 167px;
   background-repeat: no-repeat;
   background-position: center top;
   background-size: cover;
   &__img {
      display: block;
      margin: 0 auto;
      margin-bottom: -25px;
   }
   &__btn {
      display: inline-block;
      max-width: 522px;
   }
}
