.related {
   position: relative;
   padding-bottom: 36px;
   background: #fff;
   &__container {
      border: 41px solid var(--color-bg);
      max-width: 1526px;
      margin: 0 auto;
   }
}

.related-slider {
   background: #fff;

   &__wrap {
      padding: 30px 20px;
      display: flex;
      border: 1px solid #edf1f4;
   }
   &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0 42px;
   }
   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      max-width: 375px;
   }
   &__counter {
      left: 50%;
      transform: translateX(-50%);
      bottom: 46px;
   }
   .slick-arrow {
      bottom: 53px;
      width: 30px;
      height: 30px;
      z-index: 10;
      transform: translateX(0);
      transition: transform 0.3s;
   }

   .slick-prev {
      left: -80px;
      background: url("../img/svg/arrow-left.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-left-disabled.svg") no-repeat center top;
      }
   }

   .slick-prev:hover {
      transform: translateX(-5px);
      transition: transform 0.3s;
   }

   .slick-next {
      right: -80px;
      background: url("../img/svg/arrow-right.svg") no-repeat center top;

      &.slick-disabled {
         background: url("../img/svg/arrow-right-disabled.svg") no-repeat center top;
      }
   }

   .slick-next:hover {
      transform: translateX(5px);
      transition: transform 0.3s;
   }
}

.related-bottom {
   background: #fff;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 13px 20px;
   .amount {
      margin-left: auto;
      margin-right: 38px;
   }
}

.sku-wrapper {
   font-weight: 500;
   font-size: 18px;
   line-height: 20px;
   color: #606060;
   opacity: 0.7;
}
