.partners {
   padding-top: 50px;
   margin-bottom: 50px;
   &__bottom {
      text-align: center;
   }
   &__btn {
      display: inline-flex;
      max-width: 258px;
      margin-right: 100px;
   }
}

.partners-list {
   display: flex;
   flex-wrap: wrap;
   list-style-type: none;
   counter-reset: partners-counter;
   margin-right: -5%;
   margin-bottom: -33px;
   &__item {
      padding-top: 30px;
      width: 21%;
      margin-right: 4%;
      margin-bottom: 71px;
      position: relative;
      &:before {
         content: counter(partners-counter);
         counter-increment: partners-counter;
         display: block;
         position: absolute;
         top: 0;
         left: 0;
         font-weight: 600;
         font-size: 14px;
         line-height: 20px;
         letter-spacing: 0.06em;
         color: #949494;
      }
   }
   &__img {
      max-width: 143px;
      width: 100%;
      margin-bottom: 5px;
   }
   &__title {
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      max-width: 143px;
      margin: 0 auto;
      width: 100%;
   }
}
