.page-cart {
   padding-top: 24px;
   margin-bottom: 80px;
   &__wrap {
      display: flex;
      justify-content: space-between;
   }
   &__title {
      font-weight: 700;
      font-size: 42px;
      line-height: 130%;
      margin-bottom: 32px;
      text-transform: uppercase;
   }
   &__left {
      background: #fff;
      width: 66%;
      padding: 0 90px;
   }
   &__right {
      width: 32.5%;
   }
}

.shop-table {
   width: 100%;
   padding-left: 100px;
   padding-right: 100px;
   border-collapse: collapse;
   thead {
      tr {
         display: table-row;
         vertical-align: baseline;
      }
      tr th {
         font-family: "Open Sans", sans-serif;
         font-weight: 400;
         font-size: 18px;
         line-height: 20px;
         color: #8f8f8f;
         padding: 23px 10px 6px;
         text-align: left;
      }
   }
   tbody {
      tr {
         border-top: 1px solid #edf1f4;
         td {
            padding: 22px 10px 26px;
         }
      }

      .product-name {
         font-weight: 600;
         font-size: 22px;
         line-height: 130%;
         text-decoration-line: underline;
         max-width: 260px;
         transition: color 0.3s;
         &:hover {
            color: var(--color-red);
            transition: color 0.3s;
         }
      }
      .product-price,
      .product-subtotal {
         font-weight: 700;
         font-size: 28px;
         line-height: 36px;
      }
      .product-quantity {
         input {
            background: var(--color-bg);
            text-align: center;
            border: none;
            width: 71px;
            height: 52px;
            font-weight: 600;
            font-size: 18px;
            line-height: 20px;
            letter-spacing: 0.06em;
         }
      }
   }
}

.cart-total {
   background: #fff;
   padding: 30px 50px 50px;
   margin-bottom: 30px;
   &__title {
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      margin-bottom: 42px;
   }
   &__top {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      margin-bottom: 57px;
   }
   &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      margin-bottom: 50px;
   }
   &__count {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 170%;
      color: #8f8f8f;
   }
   &__price {
      font-weight: 700;
      font-size: 28px;
      line-height: 36px;
      text-align: right;
   }
   &__all {
      font-family: "Open Sans", sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 170%;
      letter-spacing: 0.05em;
      &-price {
         font-weight: 900;
         font-size: 42px;
         line-height: 36px;
         text-align: right;
      }
   }
   &__btn {
      color: var(--color-red);
      max-width: 100%;
      border: 1px solid var(--color-red);
   }
   &__return {
      text-align: center;
      transition: color 0.3s;

      a {
         font-weight: 500;
         font-size: 22px;
         line-height: 20px;
         letter-spacing: 0.06em;
         color: #8f8f8f;
         border-bottom: 1px solid #8f8f8f;
         transition: color 0.3s, border-color 0.3s;
         &:hover {
            color: var(--color-dark);
            border-bottom: 1px solid var(--color-dark);
            transition: color 0.3s, border-color 0.3s;
         }
      }
   }
}
