.category {
   padding-top: 250px;
   padding-bottom: 84px;
   background: #fff;
   &__title {
      max-width: max-content;
      padding-bottom: 21px;
      margin-bottom: 30px;
      &:after {
         width: 73%;
      }
   }
   &__list {
      display: flex;
      padding-top: 37px;
      padding-right: 65px;
   }

   &__scroll {
      top: -19px;
      width: 570px;
      &:before {
         content: "03";
      }
   }
}

.category-item {
   text-align: center;
   width: 33%;
   background-repeat: no-repeat;
   background-position: right top;
   cursor: pointer;
   position: relative;
   transition: background-image 0.5s;
   &:hover {
      .link {
         color: var(--color-red);
         border: 2px solid currentColor;
         transition: color 0.3s, border 0.3s;
      }
      .category-item__title {
         color: var(--color-red);
      }
      &:nth-child(1) {
         background-image: url("../img/svg/category-red-1.svg");
      }
      &:nth-child(2) {
         background-image: url("../img/svg/category-red-2.svg");
      }
      &:nth-child(3) {
         background-image: url("../img/svg/category-red-3.svg");
      }
   }
   &:nth-child(1) {
      background-image: url("../img/svg/category-1.svg");
   }
   &:nth-child(2) {
      background-image: url("../img/svg/category-2.svg");
   }
   &:nth-child(3) {
      background-image: url("../img/svg/category-3.svg");
   }
   &__picture {
      height: 312px;
      margin-bottom: 10px;
      img {
         height: 100%;
         object-fit: contain;
      }
   }
   &__title {
      font-weight: 700;
      font-size: 26px;
      line-height: 32px;
      text-align: center;
      letter-spacing: 0.09em;
      text-transform: uppercase;
      margin-bottom: 24px;
      transition: color 0.3s;
   }
   &__link {
      border: 2px solid var(--color-dark);
      color: var(--color-dark);
   }
}
