.instruction {
   padding-top: 29px;
   text-align: right;
   &__title {
      &:before {
         background: url("../img/svg/instruction.svg") no-repeat center center;
      }
   }
}

.instruction-list {
   background: #fff;
   display: flex;
   padding: 63px 90px;
   &__item {
      max-width: 350px;
      &:not(:last-child) {
         margin-right: 116px;
      }
   }
   &__img {
      text-align: center;
      margin-bottom: 20px;
   }
   &__title {
      font-weight: 700;
      font-size: 22px;
      line-height: 32px;
      text-align: center;
      letter-spacing: 0.09em;
      color: #e44747;
   }
}
