.reviews {
   padding-bottom: 20px;
   &__title {
      &:before {
         background: url("../img/svg/reviews.svg") no-repeat center center;
      }
   }
}

.reviews-content {
   padding-bottom: 23px;
   margin-bottom: 30px;
   display: flex;
   justify-content: space-between;
   background: #fff;

   &__title {
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      margin-bottom: 39px;
   }
}

.reviews-left {
   width: 62.5%;
   padding-top: 54px;
   padding-left: 46px;
   border-right: 1px solid #edf1f4;
}

.reviews-list {
   max-width: 810px;

   &__item {
      margin-bottom: 34px;
   }
   &__top {
      display: flex;
      margin-bottom: 16px;
   }
   &__info {
      padding: 12px 36px;
   }
   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      margin-bottom: 8px;
   }
   &__text {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 165%;
   }
   &__raiting {
      img:not(:last-child) {
         margin-right: 5px;
      }
      &-date {
         font-family: "Open Sans", sans-serif;
         font-weight: 400;
         font-size: 16px;
         line-height: 20px;
         color: #b2b2b2;
         margin-left: 13px;
      }
   }
}

.reviews-right {
   width: 37.5%;
   padding: 54px 72px 54px 45px;
   &__input {
      font-size: 22px;
      text-transform: uppercase;
      padding-bottom: 0;
   }
   &__form {
      .form__textarea {
         font-size: 22px;
         text-transform: uppercase;
      }
   }
}

.form {
   &__note {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #b2b2b2;
      margin-bottom: 36px;
   }
   &__star {
      width: 18px;
      height: 18px;
      &:not(:last-child) {
         margin-right: 6px;
      }
   }
}

.form-raiting {
   padding-bottom: 5px;
   &__wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__mark {
      font-weight: 600;
      font-size: 22px;
      line-height: 170%;
      color: #2c2d33;
      text-transform: uppercase;
      margin-right: 10px;
   }
   svg {
      width: 18px;
      height: 17px;
      cursor: pointer;
      path {
         stroke: var(--color-red);
      }
      &:hover {
         path {
            fill: var(--color-red);
         }
      }
   }
}
