.why {
   padding-top: 83px;
   margin-bottom: 70px;
}

.why-list {
   margin-bottom: 10px;
   &__item {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
   }
   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
   }
}

.sticky {
   padding: 87px 20px 20px 57px;
   position: sticky;
   top: 0;
   &__title {
      font-weight: 700;
      font-size: 32px;
      line-height: 130%;
      text-transform: uppercase;
      margin-bottom: 36px;
   }
}

.sticky-list {
   &__item {
      padding-left: 33px;
      font-weight: 800;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      margin-bottom: 30px;
      position: relative;
      &:before {
         content: "";
         display: block;
         position: absolute;
         left: 0;
         top: 50%;
         transform: translateY(-50%);
         width: 18px;
         height: 1px;
         background-color: currentColor;
      }
      &:after {
         content: "";
         display: block;
         position: absolute;
         left: 9px;
         top: 50%;
         transform: translateY(-50%);
         height: 18px;
         width: 1px;
         background-color: currentColor;
      }
      &.active {
         font-weight: 600;
         color: var(--color-red);
         &:after {
            display: none;
         }
      }
      &:hover {
         color: var(--color-red);
      }
   }
}
