.products-section {
   padding-top: 40px;
   padding-bottom: 50px;
}

.products {
   &__container {
      display: flex;
   }

   &__left {
      padding: 18px 23px 30px 30px;
      width: 25%;
      background: #fff;
   }
   &.columns-1 {
      .product {
         width: 100%;
         padding-bottom: 10px;
      }
      .card {
         position: relative;
         flex-direction: row;
         justify-content: space-between;
         padding-right: 0;
         &__top {
            margin-bottom: 40px;
         }
         &__bottom {
            width: 60%;
            margin-top: 0;
            padding-left: 15px;
            padding-right: 15px;
         }
         &__more {
            display: none;
         }
         &__raiting {
            justify-content: space-between;
         }
         &__stars {
            margin-right: 0;
         }
         &-wrap {
            width: 75%;
            display: flex;
            flex-direction: row;
         }
      }
      .card-list {
         &__item {
            display: block;
         }
      }

      .woocommerce-loop-product__title {
         max-width: 480px;
         margin-bottom: 15px;
      }
      .woocommerce-loop-product__link {
         width: 45%;
         padding-right: 10px;
      }
   }
}

.products-filter {
   margin-bottom: 27px;
   border-bottom: 1px solid #b2b2b2;

   &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 33px;

      cursor: pointer;
   }

   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
   }

   &__toggle {
      display: block;
      position: relative;
      width: 14px;
      height: 14px;
      color: var(--color-red);

      &:before {
         content: "";
         position: absolute;
         left: 50%;
         top: 0;
         width: 2px;
         height: 100%;
         background-color: currentColor;
         transform: translateX(-50%);
      }

      &:after {
         content: "";
         position: absolute;
         left: 0;
         top: 50%;
         width: 100%;
         height: 2px;
         background-color: currentColor;
         transform: translateY(-50%);
      }
   }

   &__item:not(:last-child) {
      margin-bottom: 20px;
   }

   &__bottom {
      padding-bottom: 29px;

      display: none;
   }

   &--open {
      .products-filter__toggle {
         &:before {
            display: none;
         }
      }

      .products-filter__bottom {
         display: block;
      }
   }

   &__link {
      position: relative;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #e44747;
      left: 0;
      padding: 0;
      padding-left: 33px;
      margin-top: 33px;

      &:before {
         content: "";
         position: absolute;
         left: 9px;
         top: 2px;
         width: 2px;
         height: 18px;
         background-color: currentColor;
         transform: translateX(-50%);
      }

      &:after {
         content: "";
         position: absolute;
         left: 0;
         top: 11px;
         width: 18px;
         height: 2px;
         background-color: currentColor;
         transform: translateY(-50%);
      }
   }
}

.custom-checkbox {
   display: inline-block;
   cursor: pointer;
   user-select: none;

   &__text {
      position: relative;
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      padding-left: 32px;

      &:before {
         position: absolute;
         content: "";
         left: 0;
         bottom: 0;
         top: 5px;
         width: 15px;
         height: 15px;
         border: 1px solid #b2b2b2;
         opacity: 0.4;
      }

      &:after {
         content: "";
         display: block;
         position: absolute;
         left: 2px;
         top: 0;
         right: 0;
         bottom: 0;
         opacity: 0;
      }
   }

   &__input {
      &:checked + span {
         &:before {
            border: 4px solid var(--color-red);
            opacity: 1;
         }

         &:after {
            opacity: 1;
         }
      }
   }
}

.products-price {
   margin-bottom: 27px;
   border-bottom: 1px solid #b2b2b2;

   &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 33px;
      cursor: pointer;
   }

   &__input {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
   }

   &__title {
      font-weight: 700;
      font-size: 22px;
      line-height: 150%;
   }
}

.custom-number {
   flex-shrink: 0;
   margin-bottom: 28px;

   &__text {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: #b2b2b2;
   }

   &__input {
      width: 69px;
      height: 41px;
      border: 1px solid #b2b2b2;
      padding-left: 10px;
      padding-right: 10px;
   }

   &__button {
      font-size: 18px;
      line-height: 20px;
      text-align: center;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #e44747;
      border: 2px solid #e44747;
      padding: 5px 17px;
      margin-bottom: 28px;
   }
}

.custom-range {
   position: relative;
   -webkit-appearance: none;
   width: 100%;
   height: 5px;
   outline: none;
   -webkit-transition: 0.2s;
   transition: 0.2s;
   z-index: 1;
}

.custom-range::-webkit-slider-thumb {
   -webkit-appearance: none;
   appearance: none;
   width: 30px;
   height: 30px;
   background: #e44747;
   border-radius: 50%;
   cursor: pointer;
   z-index: 2000;
}

.custom-range::moz-range-thumb {
   -webkit-appearance: none;
   appearance: none;
   width: 30px;
   height: 30px;
   background: #e44747;
   border-radius: 50%;
   cursor: pointer;
}

.range-block {
   position: relative;
}

#progressBar {
   width: 100%;
   height: 2px;
   position: absolute;
   top: 12px;
   background: #e44747;
   border-radius: 3px;
   z-index: 1;
}

.products-right {
   width: 75%;
   background: #fff;
   border: 1px solid #edf1f4;

   &__top {
      padding: 24px 24px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #edf1f4;
   }
}

.products {
   display: flex;
   flex-wrap: wrap;
   margin-bottom: 10px;
}

.icons {
   &__btn {
      margin-left: 13px;

      &.active {
         svg {
            rect {
               stroke: transparent;
               fill: var(--color-red);
            }
         }
      }

      svg {
         rect {
            stroke: var(--color-dark);
            fill: transparent;
         }
      }
   }
}

.card {
   display: flex;
   flex-direction: column;
   height: 100%;
   &.open {
      .woocommerce-loop-product__link {
         height: 0;
         transform: scaleY(0);
         transition: all 0.3s;
      }
      .card__bottom {
         margin-top: 0;
      }
      .card-wrap {
         height: auto;
      }
      .card-list {
         margin-top: 0;
      }
   }
   &__else {
      padding-top: 20px;
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      color: var(--color-dark);
   }
}

.product {
   background: #fff;
   padding: 20px 24px 0;
   border-right: 1px solid #edf1f4;
   border-bottom: 1px solid #edf1f4;
   width: 33.333%;
   display: flex;
   flex-direction: column;

   &:hover {
      box-shadow: 0px 22px 80px rgba(0, 0, 0, 0.08), 0px 10.4064px 10.0172px rgba(0, 0, 0, 0.04);
      .card__more {
         transform: rotate(180deg) translateY(-50%);
         transition: transform 0.3s;
      }
   }

   &.sale {
      .card-sale {
         display: block;
      }
   }

   &.active {
      .woocommerce-loop-product__link {
         display: none;
      }
   }
}

.woocommerce-loop-product__link {
   display: flex;
   align-items: center;
   margin-bottom: 10px;
   text-align: center;
   height: 273px;
   transform: scaleY(1);
   transition: transform 0.3s;

   img {
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}

.woocommerce-loop-product__title {
   font-weight: 700;
   font-size: 24px;
   line-height: 150%;
   margin-bottom: 27px;
   min-height: 108px;
   color: var(--color-dark);
}

.card {
   &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      position: relative;
   }

   &__bottom {
      margin-top: auto;
   }

   &__cod {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: #606060;
      opacity: 0.7;
   }

   &__raiting {
      display: flex;
      align-items: center;
      margin-bottom: 27px;

      &.reviews-none {
         .card__star {
            svg path {
               fill: #b2b2b2;
            }
         }

         .card-review {
            svg {
               display: none;
            }
         }
      }
   }

   &__stars {
      margin-right: 25px;
   }

   &__star {
      margin-right: 5px;
   }

   &__more {
      display: block;
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      background: #e44747;
      position: relative;
      margin: 0 auto;
      transform: translateY(50%);

      &:before,
      &:after {
         content: "";
         display: block;
         position: absolute;
         transform: translate(-50%, -50%);
         top: 50%;
         left: 50%;
         background-color: #fff;
      }

      &:before {
         height: 14px;
         width: 1px;
      }

      &:after {
         width: 14px;
         height: 1px;
      }

      &.active:before {
         display: none;
      }
   }

   &__last {
      text-align: center;
      justify-content: center;
      align-items: center;
      min-height: 500px;

      .woocommerce-loop-product__title {
         text-align: center;
         margin-top: 30px;
         min-height: 30px;
      }
   }
}

.promotion {
   display: flex;

   &__item {
      margin-left: 11px;

      svg path {
         transition: fill 0.3s;
      }

      &:hover {
         svg path {
            fill: var(--color-red);
            transition: fill 0.3s;
         }
      }
   }
}

.custom-radio {
   display: flex;
   justify-content: space-between;

   &__sort {
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      color: #959595;
      margin-right: 25px;
   }

   &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      position: relative;
      cursor: pointer;

      &:before {
         content: "";
         display: block;
         position: absolute;
         bottom: -2px;
         left: 0;
         width: 100%;
         height: 1px;
         background-color: transparent;
      }
   }

   &__group {
      &:not(:last-child) {
         margin-right: 23px;
      }
   }

   &__input {
      &:checked + span {
         &:before {
            border: 4px solid var(--color-red);
            opacity: 1;
         }

         &:after {
            opacity: 1;
         }
      }
   }
}

.custom-radio__input[type="radio"]:checked + label {
   color: #e44747;

   &:before {
      background-color: #e44747;
   }
}

.card-sale {
   position: absolute;
   bottom: -30px;
   left: 0;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   text-align: center;
   letter-spacing: 0.06em;
   color: #fff;
   display: none;

   &__left {
      background-color: #e44747;
      border-radius: 2px;
      margin-right: 5px;
      padding: 4px 29px;
   }

   &__right {
      background: #2b4873;
      border-radius: 2px;
      padding: 4px 20px;
   }
}

.card-review {
   display: flex;
   align-items: center;

   svg {
      margin-right: 12px;
   }

   &__count {
      margin-right: 3px;
   }

   &__text {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #606060;

      &:hover {
         color: #000;
         transition: color 0.3s;
      }
   }
}

.amount {
   font-weight: 700;
   font-size: 36px;
   line-height: 36px;
   flex-shrink: 0;
   margin-right: 10px;
}

.card-wrap {
   display: flex;
   flex-direction: column;
   height: 100%;
}

.card-price {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-bottom: 30px;
}

.card-list {
   margin-top: auto;
   margin-bottom: -12px;

   &__cat {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: #b2b2b2;
      flex-shrink: 0;
   }

   &__res {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      flex-shrink: 0;
   }

   &__item {
      display: none;
      justify-content: space-between;
      margin-bottom: 18px;

      &:first-child {
         display: flex;
      }
   }

   &__dots {
      width: 100%;
      border-bottom: 2px dotted #b2b2b2;
   }

   &.active {
      .card-list__item {
         display: flex;
      }
   }
}

.pagination {
   display: flex;
   justify-content: space-between;
   padding: 20px 16px;

   &__arrow {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 0.06em;
      color: var(--color-dark);
      transition: color 0.3s;
      &:hover {
         color: var(--color-red);
         svg {
            path {
               fill: var(--color-red);
               transition: fill 0.3s;
            }
         }
      }

      svg {
         margin-right: 8px;
         margin-left: 8px;
      }

      &.disabled {
         color: #949494;

         svg path {
            fill: #949494;
         }
      }
   }

   &-list {
      display: flex;
      justify-content: center;

      &__item {
         display: flex;
         justify-content: center;
         align-items: center;
         font-weight: 600;
         font-size: 18px;
         line-height: 20px;
         letter-spacing: 0.06em;
         color: #434343;
         width: 32px;
         height: 32px;
         cursor: pointer;
         transition: color 0.3s;

         &:not(:last-child) {
            margin-right: 25px;
         }

         &.active {
            border: 2px solid var(--color-red);
            color: var(--color-red);
         }
         &:hover {
            color: var(--color-red);
         }
      }
   }
}
